body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/* 
Utility classes

Usually use of !important is a red flag. Here it's a proactive 
use of !important which isn't. This project doesn't actually need it
but in terms of a scaling app it's useful in the long run. 

-- important is useful in the long run? Crazy Right?
Well, I have a whole talk on it relevant stuff is from slide 52
https://bit.ly/2Orz62W

tl;dr it's similar to OOCSS but not.

*/

.display-flex {
  display: -webkit-flex !important;
  display: flex !important
}

.display-inline-block {
  display: inline-block !important
}

.flex-grow-1 {
  -webkit-flex-grow: 1 !important;
          flex-grow: 1 !important
}

.flex-direction-column {
  -webkit-flex-direction: column !important;
          flex-direction: column !important
}
.repo-cell {
  padding: 1em;
  -webkit-flex-grow: 1;
          flex-grow: 1
}

.repo-cell:first-of-type {
  width: 9em;
}

.repo-cell .label {
  opacity: 0.1;
  transition: opacity 300ms ease;
}

.repo-row:hover .repo-cell .label {
  opacity: 1
}


img.commit-author {
  max-height: 2em;
  margin-right: 1em;
  border-radius: 3px;
}

.commits {
  margin-bottom: 1em;
  background-color: #eee;
  padding: 1em 1em 2em;
}

.commits .commit {
  margin-bottom: 1em;
}
.repo-row {
  display: -webkit-flex;
  display: flex;
  padding-bottom: 1em;
}

.repo-row .rotate-90 {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

.repo-row-commit-holder {
  height: 0;
  overflow: hidden;
}
.layout-base header {}
.layout-base .body {
  margin: 0 3vw;
}

input {
  height: 40px;
  font-size: 20px;
  width: 100%;
  text-align: center;
}

button {
  cursor: pointer;
}
